<template>
    <div>
        <header class="white">
            <div class="wr">
                <nav>
                    <div class="logo">
                        <img src="../assets/images/gratia_logo.png" alt="">
                        <h5 class="title">Gratia</h5>
                        <h5 class="menu"><a href="" @click.prevent="display_change(false)">Gratia</a></h5>
                    </div>
                    <transition mode="out-in" duration="300" name="fade">
                        <ul class="breakpoint_nav" v-if="this.$store.state.display">
                            <li><router-link v-on:click.native="display_change(false); change(1);" to="/">Main page</router-link></li>
                            <li>Menu</li>
                            <li>Gallery</li>
                            <li>Contact</li>
                        </ul>
                    </transition>
                    <ul class="nav">
                        <li><router-link to="/">Main page</router-link></li>
                        <li>Menu</li>
                        <li>Gallery</li>
                        <li>Contact</li>
                    </ul>
                </nav>
            </div>
        </header>
        <div class="perfection">
            <div class="wr">


                    <h3>
                    MENU
                    </h3>



                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
            </div>
        </div>

        <transition mode="out-in" duration="300" name="fade">

        <div v-if="this.$store.state.page==1" key="one">
        <meal_list/>
        </div>

        <div v-if="this.$store.state.page==2" key="two">
        <meal/>
        </div>
        </transition>
        <footer>
            <div class="fork">

                <hr>
                <span></span>
                <hr>

            </div>

            <div class="copyrights">
                <h5>Gratia Restaurant</h5>
                <p>Copyright 2016. All rights reserved by symu.<br/>
                    HTML5, CSS3, Responsive Markup. Built with Vue.Js Framework. Daniel Yakovenko.</p>
                <hr>
                <span class="social">
                <img src="../assets/images/FB.png" alt="">
                <img src="../assets/images/TR.png" alt="">
                <img src="../assets/images/IG.png" alt="">
            </span>
            </div>

        </footer>
    </div>




</template>

<script>
    import meal_list from '../components/meal_list.vue'
    import meal from '../components/meal.vue'
    import book from '../components/book_component.vue'
    export default {

        name: "menu_b",
        components: {meal_list, meal, book},
        data(){
            return {
                show: true
            }
        },
        methods: {
            change(pos) {
                //console.log(pos);
                this.$store.dispatch('change', pos).then(res => {
                    //console.log(res);

                }, err => {
                    alert(err);
                });

            },
            display_change(p) {
                if(!p) {
                    this.$store.dispatch('display_change').then(res => {
                        //console.log(res);

                    }, err => {
                        alert(err);
                    });
                }
                else {
                    this.$store.dispatch('display_change', p).then(res => {
                        //console.log(res);

                    }, err => {
                        alert(err);
                    });
                }

            }
        }
    }
</script>

<style scoped lang="css">
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active   2.1.8 */ {
        opacity: 0;
    }
</style>


