<template>
    <div>
        <section class="meal">

            <div class="wr">
                <h5><a href="" href="" @click.prevent="change(1)">SEE ALL MENU LIST</a></h5>
                <div class="meal_item">
                    <div
                     :style="{'background-image': 'url(' + require('../assets/images/chikenburger.jpg') + ')'}">

                    </div>


                    <div>

                        <div class="meal_name">
                        <h2>Chicken burger</h2>
                        <hr>
                        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
                        </div>
                        <div class="components">
                            <h5>Components</h5>
                            <p>chicken breast fillet, marinated overnight accompained with tomato
                                cos lettuce & Spanish red onion</p>
                        </div>
                        <div class="price">
                            <h3>5$</h3>
                            <p>Total cost</p>
                        </div>
                    </div>
                </div>

            </div>

        </section>

    </div>
</template>

<script>
    export default {
        name: "meal",
        methods: {
            change(pos) {
                //console.log(pos);
                this.$store.dispatch('change', pos).then(res => {
                    //console.log(res);

                }, err => {
                    alert(err);
                });

            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../assets/css/config";
.meal {
    &>.wr {
        margin-bottom: 50px;

        &>h5 {
            width: 100%;
            max-width: 1090px;
            margin: 20px auto;
            font-size: 17px;

            @media screen and (max-width: $small) {
                text-align: center;
            }


            &>a {
                color: #6a6a6a;
                transition: all 0.5s ease;

                &:hover {
                    color: #000000;
                }
            }



        }


    &>.meal_item {
        max-width: 1090px;
        margin: 0 auto;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        box-shadow: 0px 0px 80px -10px rgba(0,0,0,0.2);

        @media screen and (max-width: $small) {
            flex-direction: column;
        }


        &>div {

            background: no-repeat center/cover;
            width: 545px;
            height: 700px;
            max-height: 700px;
            display: block;
            @media screen and (max-width: $small) {
                width: 100%;
                height: 500px;
            }

            &>.meal_name {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                padding: 60px 0;
                border-bottom: 1px solid #e7e8e9;

                @media screen and (max-width: $small) {
                    padding: 25px 0;
                }

                &>h2 {
                    color: #151b29;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 36px;
                    margin: 0;
                    @media screen and (max-width: $small) {
                        font-size: 24px;
                    }
                }
                &>hr {
                    width: 40px;
                    height: 1px;
                    border: none;
                    background-color: #e7e8e9;
                    margin: 30px 0;
                    @media screen and (max-width: $small) {
                        margin: 15px 0;
                    }
                }
                &>p {
                    color: #8b909d;
                    font-family: 'Crimson', serif;
                    font-size: 20px;
                    font-style: italic;
                    margin: 0;
                    text-align: center;

                    @media screen and (max-width: $small) {
                        font-size: 18px;
                    }
                }
            }
            &>.components {
                padding: 60px 25px;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                border-bottom: 1px solid #e7e8e9;

                @media screen and (max-width: $small) {
                    padding: 25px 25px;
                }

                @media screen and (max-width: 950px) {
                    flex-direction: column;
                }
                &>h5 {
                    color: #151b29;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 20px;
                    margin: 0 20px 0 0;
                    @media screen and (max-width: 950px) {
                        text-align: center;
                        margin: 0;
                    }
                }
                &>p {

                    color: #8b909d;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 25px;

                    @media screen and (max-width: 950px) {
                        text-align: center;
                    }
                }
            }

            &>.price {

                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                padding: 40px 0;
                @media screen and (max-width: $small) {
                    padding: 20px 0;
                }

                &>h3 {
                    color: #829c2c;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 64px;
                    font-weight: 700;
                    margin: 0;

                }

                &>p {
                    color: #8b909d;
                    font-family: 'Crimson', serif;
                    font-size: 24px;
                    font-style: italic;
                    margin: 0;

                }
            }




        }
    }
    }
}
</style>


