<template>
    <div>
        <header class="white">
            <div class="wr">
                <nav>
                    <div class="logo">
                        <img src="../assets/images/gratia_logo.png" alt="">
                        <h5 class="title">Gratia</h5>
                        <h5 class="menu"><a href="" @click.prevent="display_change(false)">Gratia</a></h5>
                    </div>
                    <transition mode="out-in" duration="300" name="fade">
                        <ul class="breakpoint_nav" v-if="this.$store.state.display">
                            <li><router-link v-on:click.native="display_change(false); change(1);" to="/">Main page</router-link></li>
                            <li><router-link v-on:click.native="display_change(false); change(1);" to="/menu">Menu</router-link></li>
                            <li>Gallery</li>
                            <li>Contact</li>
                        </ul>
                    </transition>
                    <ul class="nav">
                        <li><router-link v-on:click.native="change(1)" to="/">Main page</router-link></li>
                        <li><router-link v-on:click.native="change(1)" to="/menu">Menu</router-link></li>
                        <li>Gallery</li>
                        <li>Contact</li>
                    </ul>
                </nav>
            </div>
        </header>
        <div class="perfection">
            <div class="wr">


                <h3 >
                    BOOK A TABLE
                </h3>


                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
            </div>
        </div>

        <section class="booking">
        <div class="wr">

            <input type="text" placeholder="DD" class="line-3">
            <input type="text" placeholder="MM" class="line-3">
            <input type="text" placeholder="YYYY" class="line-3">
            <input type="text" placeholder="HOUR" class="line-2">
            <input type="text" placeholder="NUMBER OF PEOPLE" class="line-2">
            <input type="text" placeholder="FIRST NAME AND LAST NAME" class="half-1">
            <input type="text" placeholder="PHONE NUMBER" class="half-2">
            <input type="text" placeholder="E-MAIL" class="half-1">
            <button class="half-2 black">PREPARE MENU</button>
            <textarea name="" id="" cols="30" rows="10" placeholder="Your message..."></textarea>
            <button>SEND</button>

        </div>
    </section>

        <footer>
            <div class="fork">

                <hr>
                <span></span>
                <hr>

            </div>

            <div class="copyrights">
                <h5>Gratia Restaurant</h5>
                <p>Copyright 2016. All rights reserved by symu.<br/>
                    HTML5, CSS3, Responsive Markup. Built with Vue.Js Framework. Daniel Yakovenko.</p>
                <hr>
                <span class="social">
                <img src="../assets/images/FB.png" alt="">
                <img src="../assets/images/TR.png" alt="">
                <img src="../assets/images/IG.png" alt="">
            </span>
            </div>

        </footer>


    </div>
</template>

<script>
    export default {
        name: "book_component",
        methods: {
            change(pos) {
                //console.log(pos);
                this.$store.dispatch('change', pos).then(res => {
                   // console.log(res);

                }, err => {
                    alert(err);
                });

            },
            display_change(p) {
                if(!p) {
                    this.$store.dispatch('display_change').then(res => {
                        //console.log(res);

                    }, err => {
                        alert(err);
                    });
                }
                else {
                    this.$store.dispatch('display_change', p).then(res => {
                        //console.log(res);

                    }, err => {
                        alert(err);
                    });
                }

            }
        }
    }
</script>

<style scoped lang="scss">
.booking {
    width: 100%;
    margin: 0 0 50px 0;

    &>.wr {
        max-width: 1400px;
        display: grid;
        grid-template-columns: 15% 15% 15% 23% 23%;
        grid-template-rows: 4fr;
        grid-row-gap:30px;
        grid-column-gap:2%;


        @media screen and (max-width: 850px) {
            grid-template-columns: 15% 15% 15% 15% 15% 15%;
            grid-template-rows: 8fr;
        }


        &>input {
            font-family: 'Montserrat', sans-serif;
            font-weight: 700;
            background-color: #fff;
            outline: none;
            text-align: center;
            padding: 12px 10px;
            border: 0;
            box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.15);
            @media screen and (max-width: 850px) {
                text-align: left;
                font-size: 10px;
            }


        }
        &>.line-3 {
            @media screen and (max-width: 850px) {
                grid-column-start: auto;
                grid-column-end: span 2;
            }
        }
        &>.line-2 {
            @media screen and (max-width: 850px) {
                grid-column-start: auto;
                grid-column-end: span 3;
            }
        }

        &>.half-1 {
            text-align: left;
            grid-column-start: 1;
            grid-column-end: 4;
            @media screen and (max-width: 850px) {
                grid-column-start: 1;
                grid-column-end: 7;
            }

        }
        &>.half-2 {
            text-align: left;
            grid-column-start: 4;
            grid-column-end: 6;

            @media screen and (max-width: 850px) {
                grid-column-start: 1;
                grid-column-end: 7;
            }
        }


        &>.black {
            background-color: #000000;
            color: #d89b64;
            font-family: 'Montserrat', sans-serif;
            font-weight: 700;
            outline: none;
            border: none;
            cursor: pointer;
            box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.15);
            &:hover {
                background-color: #000000;
                color: #ffffff;
            }
            @media screen and (max-width: 850px) {
                text-align: center;
            }
        }

        &>textarea {
            grid-column-start: 1;
            grid-column-end: 6;
            background-color: #fff;
            font-family: 'Montserrat', sans-serif;
            font-size: 16px;
            outline: none;
            padding: 10px;
            box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.15);
            border: none;
            @media screen and (max-width: 850px) {
                grid-column-start: 1;
                grid-column-end: 7;
            }



        }
        &>button {
            grid-column-start: 1;
            grid-column-end: 6;
            padding: 12px 10px;
            background-color: #d89b64;
            color: #ffffff;
            font-family: 'Montserrat', sans-serif;
            font-weight: 700;
            outline: none;
            border: none;
            cursor: pointer;
            box-shadow: 0px 0px 30px 0px rgba(0,0,0,0.15);
            transition: all 0.3s ease;

            &:hover {
                background-color: #ffffff;
                color: #d89b64;
            }
            @media screen and (max-width: 850px) {
                grid-column-start: 1;
                grid-column-end: 7;

            }

        }

    }

}
</style>


