<template>
    <div>

<section class="meal_list">
    <div class="wr">
        <div @click.prevent="change(2); display_change(true);" >
        <div :style="{'background-image': 'url(' + require('../assets/images/main_dishes.jpg') + ')'}"></div>
        <h3>MAIN DISHES</h3>
        <hr>
        <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
    </div>
        <div @click.prevent="change(2)" >
            <div :style="{'background-image': 'url(' + require('../assets/images/soups.jpg') + ')'}"></div>
            <h3>SOUPS</h3>
            <hr>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
        </div>
        <div @click.prevent="change(2)" >
            <div :style="{'background-image': 'url(' + require('../assets/images/hamburgers.jpg') + ')'}"></div>
            <h3>HAMBURGERS</h3>
            <hr>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
        </div>
        <div @click.prevent="change(2)" >
            <div :style="{'background-image': 'url(' + require('../assets/images/desserts_meal.jpg') + ')'}"></div>
            <h3>DESSERTS</h3>
            <hr>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
        </div>
        <div @click.prevent="change(2)" >
            <div :style="{'background-image': 'url(' + require('../assets/images/barbecue.jpg') + ')'}"></div>
            <h3>BARBECUE</h3>
            <hr>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
        </div>
        <div @click.prevent="change(2)" >
            <div :style="{'background-image': 'url(' + require('../assets/images/salads.jpg') + ')'}"></div>
            <h3>SALADS</h3>
            <hr>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit.</p>
        </div>

    </div>

</section>

    </div>
</template>

<script>


    export default {
        name: "meal_list",
        methods: {
            change(pos) {
                //console.log(pos);
                this.$store.dispatch('change', pos).then(res => {
                    //console.log(res);

                }, err => {
                    alert(err);
                });

            },
            display_change(p) {
                if(!p) {
                    this.$store.dispatch('display_change').then(res => {
                        //console.log(res);

                    }, err => {
                        alert(err);
                    });
                }
                else {
                    this.$store.dispatch('display_change', p).then(res => {
                        //console.log(res);

                    }, err => {
                        alert(err);
                    });
                }

            }
        }
    }
</script>

<style scoped lang="scss">
    .meal_list {
        &>.wr {

            max-width: 1185px;
            margin-bottom: 50px;

        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        grid-auto-rows: 1fr;
            grid-gap: 30px;
            align-items: center;

            &>div {
                max-width: 500px;
                height: 450px;
                background-color: #ffffff;
                cursor: pointer;
                box-shadow: 0px 0px 50px -10px rgba(0,0,0,0.5);
                transition: transform 0.5s ease;

                &:hover {
                    transform: scale(1.05,1.05);
                }

                &>div {
                    width: 100%;
                    height: 280px;
                    background: no-repeat center/cover;
                }
                &>h3 {
                    font-family: 'Montserrat', sans-serif;
                    font-size: 30px;
                    text-align: center;
                    margin: 25px 0;
                    padding: 0 10px;
                }
                &>hr {
                    width: 40px;
                    height: 1px;
                    border: none;
                    background-color: #e7e8e9;
                }
                &>p {
                    font-family: 'Crimson', serif;
                    font-style: italic;
                    color: #8b909d;
                    font-size: 18px;
                    text-align: center;
                    padding: 0 10px;
                }


            }
    }
    }

</style>


