<template>
  <div id="app">
      <transition mode="out-in" duration="300" name="fade" @before-enter="beforeEnter" appear>
          <router-view/>
      </transition>

  </div>
</template>

<script>
export default {
  name: 'App',
    methods: {
        beforeEnter () {
            this.$root.$emit('scrollBeforeEnter');
        }
    }
}
</script>

<style>


</style>



