

import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/main_page'
import menu_block from '@/components/menu_b'
import book_component from '@/components/book_component'
import {store} from "../store";


Vue.use(Router)

const router = new Router({

    mode: "hash",
    routes: [
        {
            path: '/',
            name: 'home',
            component: home
        },
        {
            path: '/menu',
            component: menu_block
        },
        {
            path: '/book',
            component: book_component
        }
    ],
    scrollBehavior (to, from, savedPosition) {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                resolve( { x: 0, y: 0 } )
            }, 300)
        })
    }
})


export default router;



