import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);


export const store = new Vuex.Store({
    state: {
        page: 1,
        display: false
    },
    mutations: {

    },
    actions: {
        change(context, data) {

            return this.state.page=data;
        },
        display_change (context, p) {
            if(!p) {
            return this.state.display =! this.state.display;
            }
            else {this.state.display = false;}
        }
    },
    getters: {},
})


